* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("../82073184.5b388fda.png");
  background-size: 500px;
  background-attachment: fixed;
  margin: 0;
}

body p {
  font-family: Roboto Mono, monospace;
}

header {
  height: 70px;
  z-index: 1;
  background-color: #000;
  padding-right: 10px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

nav img {
  height: 50px;
}

nav {
  justify-content: space-between;
  align-items: center;
  padding: 10px 10px 10px 0;
  font-family: Pixelify Sans, sans-serif;
  display: flex;
}

h2, h3, h4 {
  font-family: Pixelify Sans, sans-serif;
}

nav div {
  align-items: center;
  display: flex;
}

header h1 {
  color: #fff;
  margin: 0;
  font-size: 1rem;
}

nav ul {
  margin: 0;
  display: none;
}

main {
  margin: 90px 0;
}

footer {
  height: 70px;
  z-index: 1;
  color: #fff;
  background-color: #000;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
  font-size: 12px;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

footer a {
  color: #fff;
}

footer p {
  margin: 0;
}

footer ul {
  width: 400px;
  justify-content: space-around;
  padding: 0;
  list-style: none;
  display: flex;
}

@media screen and (min-width: 993px) {
  header h1 {
    color: #fff;
    font-size: 3vh;
  }

  header .menu-icon {
    display: none;
  }

  nav ul {
    color: #fff;
    width: 70%;
    justify-content: space-around;
    font-size: 1.5rem;
    list-style: none;
    display: flex;
  }

  header {
    height: 150px;
    padding: 10px 0;
  }

  nav {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
  }

  nav img {
    height: 50px;
  }

  nav a {
    color: #fff;
    text-decoration: none;
  }

  nav li:hover {
    transition: all 1s;
    transform: scale(1.5);
  }

  nav a:hover {
    color: #e19200;
  }
}

.transparent {
  width: 100%;
  height: 10vh;
  border: 1px solid red;
}

.socialmedia {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
}

.socialmedia a {
  text-align: center;
  color: #fff;
  padding: 16px;
  font-size: 25px;
  display: block;
}

.socialmedia a:hover {
  background: #000;
}

.whatsapp {
  color: #fff;
  background: #01cb37;
}

.instagram {
  color: #fff;
  filter: progid:DXImageTransform. Microsoft. gradient( startColorstr= "#f09433", endColorstr= "#bc1888", GradientType= 1 );
  background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}

.facebook {
  color: #fff;
  background: #3b5998;
}

.twitter {
  color: #fff;
  background: #007bb5;
}

.youtube {
  color: #fff;
  background: #b00;
}

.orderhere {
  height: 75px;
  width: 75px;
  color: #fff;
  background-color: #0dc600;
  border: 3px solid #fff;
  font-size: 1em;
  font-weight: 700;
  position: fixed;
  bottom: 45vh;
  right: 0;
}

.orderhere:hover {
  transition: all 1s;
  transform: scale(1.5)translateX(-20px);
}

@media (max-width: 993px) {
  .socialmedia a {
    text-align: center;
    color: #fff;
    padding: 12px;
    font-size: 20px;
    transition: all .3s;
    display: block;
  }

  .orderhere {
    display: none;
  }
}

main {
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu-sec {
  width: 70vw;
}

.card-sec {
  width: 70vw;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.card-menu {
  box-sizing: border-box;
  width: 80%;
  background-color: #e19200;
  border-radius: 15px;
  flex-direction: column;
  margin-bottom: 5vh;
  padding-bottom: 100px;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #000;
}

.title {
  color: #e19200;
  text-align: center;
  color: #920e00;
  text-shadow: 1px 1px #000;
  margin-bottom: 20px;
  font-size: 2rem;
}

.para-div {
  color: #202020;
  margin-top: 2rem;
  padding-left: 10px;
}

.menu-title {
  color: #e19200;
  text-align: center;
  text-shadow: 3px 3px #fff, -3px -3px #000;
  background-color: #000000ad;
  margin-top: 5vh;
  margin-bottom: 5vh;
  font-size: 5rem;
}

.menu-pic {
  width: 100%;
}

.ordernow {
  text-align: right;
}

.ordernow a {
  color: #fff;
  text-decoration: none;
  display: inline-block;
}

.ordernow button {
  width: 130px;
  text-align: left;
  height: 130px;
  background-color: #920e00;
  border: none;
  border-radius: 50%;
  padding-bottom: 15px;
  padding-left: 15px;
  position: absolute;
  bottom: -50px;
  right: -60px;
}

.ordernow button:hover {
  background-color: #fff;
  box-shadow: 0 0 10px #202020;
}

.ordernow button:hover a {
  color: #920e00;
  font-size: bold;
}

select {
  border-radius: 5px;
}

header i {
  font-size: 35px;
}

@media (min-width: 993px) {
  main {
    margin-top: 15vh;
  }

  .card-menu {
    width: 100%;
    flex-direction: row;
    padding-bottom: 0;
  }

  .card-menu p, label, h4 {
    font-size: 1.5rem;
  }

  .card-menu h3 {
    font-size: 3rem;
  }

  .menu-pic {
    width: 50%;
  }
}

